@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/lato/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
body {
  margin: 0 auto;
}
a {
  color: #000000;
  text-decoration: none;
}
img {
  display: block;
  min-width: 100%;
  height: auto;
}
.button {
  padding: 10px 20px;
  width: 100%;
  min-height: 40px;
  background-color: #68B738;
  color: #ffffff;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: #ffffff57;
}
.main-header {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-header {
    display: flex;
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1300px) {
  .main-header {
    width: 1300px;
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .main-header--opened,
  .main-header--closed-line {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    position: relative;
    width: 320px;
    background-color: #ffffff;
    padding: 0 20px;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .main-header__wrapper {
    width: 768px;
    display: flex;
  }
}
.main-header__logo-wrapper {
  display: flex;
  align-items: center;
  width: 190px;
  justify-content: space-between;
  align-self: normal;
  padding: 14px 0;
}
@media (min-width: 768px) {
  .main-header__logo-wrapper {
    width: 174px;
    padding: 0;
  }
}
@media (min-width: 1300px) {
  .main-header__logo-wrapper {
    width: 202px;
  }
}
.main-header__logo .main-header__logo-text {
  height: 38px;
}
@media (min-width: 768px) {
  .main-header__logo .main-header__logo-text {
    min-height: 50px;
  }
}
@media (min-width: 1300px) {
  .main-header__logo .main-header__logo-text {
    min-height: 59px;
  }
}
.main-header__logo:hover,
.main-logo-text:hover {
  transition: all 0.3s ease;
  opacity: 0.7;
}
.main-header__logo:active,
.main-logo-text:active {
  opacity: 0.6;
}
.main-header__toggle {
  position: absolute;
  content: "";
  width: 24px;
  height: 24px;
  top: 21px;
  right: 20px;
  z-index: 2;
  background-color: #ffffff;
  display: block;
  border: none;
  cursor: pointer;
}
.main-header__toggle:hover,
.main-header__toggle:active {
  opacity: 0.7;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 2px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-header--closed .main-header__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .main-header--closed .site-list__item {
    display: none;
  }
  .main-header--closed .site-list__item--active {
    display: block;
  }
  .main-header--closed .site-list__item--active a {
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
  }
  .main-header--opened .main-nav__wrapper {
    width: 100%;
    position: absolute;
    top: 67px;
    right: 0;
    z-index: 10;
    text-align: center;
    background-color: #ffffff;
  }
  .main-header--closed .main-nav__wrapper {
    display: none;
  }
  .main-header--nojs .main-header__toggle {
    display: none;
  }
  .main-header--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-header--nojs .site-list__item {
    display: block;
  }
  .main-header--nojs .site-list__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: center;
  }
}
.main-header--opened .main-header__toggle {
  background-color: #ffffff;
}
.main-header--opened .main-header__toggle::before,
.main-header--opened .main-header__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  right: 0;
  width: 24px;
  height: 1px;
  background-color: #000000;
}
.main-header--opened .main-header__toggle::before {
  transform: rotate(50grad);
}
.main-header--opened .main-header__toggle::after {
  transform: rotate(-50grad);
  box-shadow: none;
}
